<template>
  <div>
    <button @click="count++">{{ count }} {{ plusOne }}</button>
  </div>
</template>

<script>
import { h, ref, reactive, toRefs, toRef, computed, watch } from 'vue'
export default {
  props: {
    title: String
  },
  /* 
   *  props
   *  Setup  context === { attrs, slots, emit, expose }
   */
  setup(props, context) {
    //  props
    const { title } = toRefs(props)
    const title1 = toRef(props, 'title')
    const count = ref(0)
    // console.log(title.value)
    // console.log(title1.value)
    // console.log(props.title)

    //  Setup 
    // Attribute $attrs
    // console.log(context.attrs)
    //  $slots
    // console.log(context.slots)
    //  $emit
    // console.log(context.emit)
    //  property
    // console.log(context.expose)
    //  
    // 
    // context.expose()

    const publicCount = ref(0)
    const privateCount = ref(0)
    // 
    context.expose({ count: publicCount })
    
    const plusOne = computed({
      get: () => count.value + 1,
      set: (val) => {
        count.value = value - 1
      }
    })
    // 
    // const plusOne = computed(() => {
    //   onTrack(e) {
    //     debugger
    //   },
    //   onTrigger(e) {
    //     debugger
    //   }
    // })

    // reactive
    const obj = reactive({ count })
    // console.log(obj.count === count.value)

    // watch
    const state = reactive({ count: 0 })
    watch(
      () => state.count,
      (count, prevCount) => {
        console.log(count, prevCount)
      }
    )

    //  API 
    return {
      count,
      plusOne
    }
    // return () => h('div', count.value)
  },
  mounted() {
    // console.log(this.count)
  }
}
</script>

